<template>
    <div class="pb-6 mb-5" v-pull-to-refresh>
        <b-tabs nav-class="h4">
            <b-tab title="2 позиции" active>
                <div class="p-3 h4 mb-0 border-top text-center">
                    <span class="text-muted">{{ positionsTotalPrice }} ₽ · </span>
                    <span class="text-danger" v-if="status == 0">не подтвержден</span>
                </div>
                <section class="bg-white">
                    <b-container>
                        <ul class="list-unstyled">
                            <li class="pb-4" v-for="(item, index) in items" :class="{'pt-4': index == 0 }">
                                <b-row>
                                    <b-col class="col-auto">
                                       <router-link :to="{ name: 'Product', params: { id: item.id }}" class="afake">
                                           <img :src="getImageUrl(item.pic[0])"
                                                :srcset="getImageUrl(item.pic[0]) + ' 2x'"
                                                :alt="item.name"
                                                width="128"
                                                height="128"
                                                :class="{'mb-4': (index != items.length -1)}"
                                                class="rounded mr-3">
                                       </router-link>
                                    </b-col>
                                    <b-col class="pl-0" :class="{'border-bottom': (index != items.length -1)}">
                                        <div class="pb-4 pl-0">
                                            <router-link :to="{ name: 'Product', params: { id: item.id }}" tag="div" class="afake text-dark strong mb-3">{{ item.category }} {{ item.name }}</router-link>
                                            <div class="h4 mb-3">
                                                {{ item.totalCount }} {{ item.units }} <span class="text-muted">× {{ item.price}} ₽ за {{ item.count }} {{ item.units }}</span>
                                            </div>
                                            <div class="strong">
                                                {{ item.totalPrice }} ₽
                                            </div>
                                        </div>
                                    </b-col>
                                </b-row>
                            </li>
                        </ul>
                    </b-container>
                </section>
            </b-tab>
            <b-tab title="Детали заказа">
                <div class="p-3 h4 mb-0 border-top text-center">
                    <span class="text-muted">{{ positionsTotalPrice }} ₽ · </span>
                    <span class="text-danger" v-if="status == 0">не подтвержден</span>
                </div>
                <div class="bg-white">
                    <b-container class="pr-0">
                        <ul class="list-unstyled">
                            <li class="pt-3 pb-3">
                                <div class="text-muted h4 mb-1">Заказ</div>
                                <div><span v-word-ends:позиция|позиции|позиций="items.length"></span> на сумму {{ positionsTotalPrice }} ₽</div>
                            </li>
                            <li class="pt-3 pb-3">
                                <div class="text-muted h4 mb-1">Оплата</div>
                                <div class="d-flex justify-content-between pr-3">
                                    <div>
                                        <span v-if="isCardPay">Картой в приложении</span>
                                        <span v-if="!isCardPay">Наличными курьеру</span>
                                    </div>
                                    <div class="h4 mb-0 text-success text-right" v-if="isCardPay">Оплачено</div>
                                </div>
                            </li>
                            <li class="pt-3 pb-3">
                                <div class="text-muted h4 mb-1">Способ получения</div>
                                <div>
                                    <span v-if="deliveryType == 0">Доставка курьером</span>
                                    <span v-if="deliveryType == 1">Самовывоз</span>
                                </div>
                            </li>
                            <li class="pt-3 pb-3">
                                <div class="text-muted h4 mb-1">
                                    <span v-if="order.deliveryType == 0">Адрес доставки</span>
                                    <span v-if="order.deliveryType == 1">Пункт самовывоза</span>
                                </div>
                                <div>
                                    <span v-if="order.deliveryType == 0">{{ deliveryAddress | FormatAddress }}</span>
                                    <span v-if="order.deliveryType == 1">{{ deliveryPointTitle }}</span>
                                </div>
                            </li>
                        </ul>
                    </b-container>
                </div>
            </b-tab>
        </b-tabs>
        <div class="bg-white p-3 mt-2 text-center">
            <b-link class="text-uppercase text-danger" @click="cancelOrder" v-if="status == 0">
                <c-icon name="cancel" class="align-middle"/>
                <span class="align-middle">Отменить заказ</span>
            </b-link>
            <b-link class="text-uppercase text-danger" @click="repeatOrder" v-if="status != 0">
                <c-icon name="repeat" class="align-middle"/>
                <span class="align-middle">Повторить заказ</span>
            </b-link>
        </div>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'order',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/order',
        repeatUrl: '/api/repeat_order',
        cancelUrl: '/api/cancel_order',

        useParamsId: true,
        date: '',
        status: null,
        isCardPay: null,
        deliveryType: null,
        items: [],
      }
    },

    computed: {
      cart() {
        return this.$store.getters['global/user'].cart || [];
      },

      order() {
        return this.$store.getters['global/user'].order || {};
      },

      delivery() {
        return this.$store.getters['global/user'].delivery || [];
      },

      positionsTotalPrice() {
        if (!this.items) return 0;
        return this.items.reduce((total, current) => total + parseFloat(current.totalPrice), 0);
      },

      deliveryPointTitle() {
        if (!this.order || typeof this.order.deliveryPoint === 'undefined') return '';
        return this.shop.deliveryPoints.sort((item) => item.id == this.order.deliveryPoint)[0].text;
      },

      deliveryAddress() {
        if (typeof this.delivery === 'undefined' || typeof this.order.deliveryAddress === 'undefined') return '';
        return this.delivery.sort((item) => item.id == this.order.deliveryAddress)[0];
      }
    },

    methods: {
      prepareData(data) {
        this.date = data.date;
        this.status = data.status;
        this.isCardPay = data.isCardPay;
        this.deliveryType = data.deliveryType;
        this.deliveryPoint = data.deliveryPoint;
        this.items = data.items;
        this.setSubtitle(this.date);
      },

      getImageUrl(url) {
        if (!url) return;
        return require(`../../${url}`);
      },

      cancelOrder() {
        this.axios.post(`${this.cancelUrl}/${this.$route.params.id}`)
          .then(response => {
            this.isLoadDisabled = false;
            this.$router.push({path: '/orders'});
          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      },

      repeatOrder() {
        this.axios.post(`${this.repeatUrl}/${this.$route.params.id}`)
          .then(response => {
            this.isLoadDisabled = false;
            this.$router.push({path: '/orders'});
          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      }
    },

    mounted() {
        this.setTitle('Заказ');
        this.setBackUrl('/orders');
    }
  }
</script>

<style lang="scss">

</style>


