<template>
    <div class="pb-6 mb-5" v-pull-to-refresh>
        <b-tabs nav-class="h4">
            <b-tab title="Активные" active>
                <div class="p-3 text-center border-top" v-show="!unconfirmed.length && !confirmed.length">
                    <span class="text-muted h4">Нет актуальных заказов</span>
                </div>
                <div class="p-3 text-center border-top" v-show="unconfirmed.length">
                    <span class="text-muted h4">Неподтвержденные</span>
                    <b-badge pill variant="danger ml-1">{{ unconfirmed.length }}</b-badge>
                </div>
                <div class="bg-white border-top border-bottom" v-show="unconfirmed.length">
                    <b-container class="pr-0">
                        <ul class="list-unstyled">
                            <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                         tag="li"
                                         v-for="(item, index) in unconfirmed"
                                         :class="{'border-bottom': (index != unconfirmed.length -1)}"
                                         class="afake text-dark pt-3 pb-3">
                                <div class="mb-1">{{ item.date }}</div>
                                <div class="text-muted"><span v-word-ends:позиция|позиции|позиций="item.count"></span> ·
                                    {{ item.price }} ₽
                                </div>
                            </router-link>
                        </ul>
                    </b-container>
                </div>
                <div class="p-3 text-center" v-show="confirmed.length">
                    <span class="text-muted h4">Подтвержденные · {{ confirmed.length }}</span>
                </div>
                <div class="bg-white border-top border-bottom" v-show="confirmed.length">
                    <b-container class="pr-0">
                        <ul class="list-unstyled">
                            <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                         tag="li"
                                         v-for="(item, index) in confirmed"
                                         :class="{'border-bottom': (index != confirmed.length -1)}"
                                         class="afake text-dark pt-3 pb-3">
                                <div class="mb-1">{{ item.date }}</div>
                                <div class="text-muted"><span v-word-ends:позиция|позиции|позиций="item.count"></span> ·
                                    {{ item.price }} ₽
                                </div>
                            </router-link>
                        </ul>
                    </b-container>
                </div>
            </b-tab>
            <b-tab title="История заказов">
                <div class="p-3 text-center border-top" v-show="!archived.length">
                    <span class="text-muted h4">Нет заказов в архиве</span>
                </div>
                <div class="bg-white border-top border-bottom">
                    <b-container class="pr-0">
                        <ul class="list-unstyled">
                            <router-link :to="{ name: 'Order', params: { id: item.id }}"
                                         tag="li"
                                         v-for="(item, index) in archived"
                                         :class="{'border-bottom': (index != archived.length -1)}"
                                         class="afake text-dark pt-3 pb-3">
                                <div class="mb-1">{{ item.date }}</div>
                                <div class="text-muted"><span v-word-ends:позиция|позиции|позиций="item.count"></span> ·
                                    {{ item.price }} ₽
                                </div>
                            </router-link>
                        </ul>
                    </b-container>
                </div>
            </b-tab>
        </b-tabs>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'orders',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/orders',

        confirmed: [],
        unconfirmed: [],
        archived: [],
      }
    },

    methods: {
      prepareData(data) {
        if (!data) return;

        this.confirmed = data.confirmed;
        this.unconfirmed = data.unconfirmed;
        this.archived = data.archived;

        this.$store.commit('global/updateUser', {
          orders: {
            confirmed: data.confirmed.length,
            unconfirmed: data.unconfirmed.length,
          }
        })
      },
    },

    mounted() {
      this.setTitle('Мои заказы');
      this.setBackUrl('/');
    }
  }
</script>

<style lang="scss">

</style>


