<template>
  <div id="app" :class="{'bg-white': layout.whiteBg}">
    <c-header v-show="!layout.rawTemplate"
              :title="title"
              :subtitle="subtitle"
              :phone="phoneNumber"
              :backUrl="backUrl"
              :homePage="layout.homePage"
              :transparent="layout.transparentHeader"
              :inverseControls="layout.inverseHeaderControls"
              :hidePhone="layout.hideHeaderPhone"
              :showClose="layout.showHeaderClose"/>
    <main role="main">
        <router-view/>
    </main>
    <c-footer v-show="!layout.customFooter && !layout.rawTemplate">
        <c-user-menu :cart="totalCart" :orders="totalOrders"/>
    </c-footer>
  </div>
</template>

<script>
import FetchHelper from './mixins/FetchHelper.js';
import RouteHelper from './mixins/RouteHelper.js';

export default {
  name: 'app',
  mixins: [FetchHelper, RouteHelper],
  data() {
    return {
      fetchUrl: '/api/config',
      phoneNumber: '',
    };
  },
  computed: {
    totalCart() {
      if (!this.$store.getters['global/user'].cart) return null;
      return this.$store.getters['global/user'].cart.length;
    },
    orders() {
      return this.$store.getters['global/user'].orders;
    },
    totalOrders() {
      if (!this.orders) return 0;
      return this.orders.confirmed + this.orders.unconfirmed;
    }
  },
  methods: {
      prepareData(data) {
          if (!data) return;

          this.$store.commit('global/setShop', data.shop);
          this.$store.commit('global/setUser', data.user);

          this.phoneNumber = data.shop.phone;
      }
  },
}
</script>

<style lang="scss">
  @import 'bootconf.scss';
  @import '~bootstrap/scss/bootstrap';

  #app {
      min-height: 100vh;
  }
</style>


