<template>
    <div>
        <section class="border-bottom pt-5">
            <b-container class="mt-2">
                <b-form-group label-for="card_number" :state="!$v.cardNumber.$error">
                    <b-form-input id="card_number"
                                  type="text"
                                  required
                                  v-model="cardNumber"
                                  v-mask="'#### #### #### ####'"
                                  v-floating-placeholder
                                  placeholder="Номер карты">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="card_name" :state="!$v.cardName.$error">
                    <b-form-input id="card_name"
                                  type="text"
                                  required
                                  v-model="cardName"
                                  v-floating-placeholder
                                  placeholder="Имя на карте">
                    </b-form-input>
                </b-form-group>
                <b-row>
                    <b-col class="pr-1">
                        <b-form-group class="mb-4"
                                      :state="!$v.cardDate.$error"
                                      label-for="card_date">
                            <b-form-input id="card_date"
                                          type="text"
                                          required
                                          v-model="cardDate"
                                          v-floating-placeholder
                                          placeholder="Срок действия">
                            </b-form-input>
                        </b-form-group>
                    </b-col>
                    <b-col class="pl-1">
                        <b-form-group class="mb-4"
                                      :state="!$v.cardCode.$error"
                                      label-for="card_code">
                            <b-form-input id="card_code"
                                          type="password"
                                          required
                                          v-model="cardCode"
                                          v-floating-placeholder
                                          placeholder="Секретный код">
                            </b-form-input>
                            <span title="Подсказка"><c-icon name="help" class="text-muted"/></span>
                        </b-form-group>
                    </b-col>
                </b-row>
                <div class="text-center mb-4">
                    <strong class="text-uppercase text-danger afake">Сканировать карту</strong>
                </div>
            </b-container>
        </section>
        <div class="p-1 bg-light"></div>
        <section class="pt-3 pb-4">
           <b-container>
               <b-form-group class="mb-6">
                   <label for="inputPassword" class="pl-0 col col-auto col-form-label">Сохранить карту</label>
                   <b-form-checkbox class="float-right" v-model="isSaveCard"></b-form-checkbox>
               </b-form-group>
               <figure class="text-center mb-3">
                   <img src="../../assets/img/pay-logos.png"
                        srcset="../../assets/img/pay-logos@2x.png 2x"
                        alt="Платежные системы"
                        width="230"
                        height="50"
                        class="mb-4">
               </figure>
               <div class="text-center text-muted small pb-6">
                   Платеж безопасен. Мы не храним данные карт, все операции проводит CloudPayments — платежная система, сертифицированная по международному стандарту безопасности PCI DSS
               </div>
           </b-container>
        </section>
        <c-footer>
            <b-button variant="danger" size="lg" block class="rounded-0" @click="PayByCard">Заплатить</b-button>
        </c-footer>
    </div>
</template>

<script>
  import { required, minLength } from 'vuelidate/lib/validators';
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'delivery',
    mixins: [RouteHelper],
    data() {
      return {
        postUrl: '/api/pay_order',
        isLoadDisabled: false,

        cardNumber: '',
        cardName: '',
        cardDate: '',
        cardCode: '',

        isSaveCard: false,
      }
    },

    computed: {
      cart() {
        return this.$store.getters['global/user'].cart || [];
      },

      card() {
        return this.$store.getters['global/user'].card || {};
      },

      order() {
        return this.$store.getters['global/user'].order || {};
      },

      positionsTotalPrice() {
        return this.cart.reduce((total, current) => total + parseFloat(current.totalPrice), 0);
      }
    },

    validations() {
      return {
        cardNumber: {
          required
        },
        cardName: {
          required
        },
        cardDate: {
          required
        },
        cardCode: {
          required
        }
      };
    },

    methods: {
      fillStep() {
        this.cardNumber = this.card.number;
        this.cardName = this.card.name;
        this.cardDate = this.card.date;
        this.cardCode = this.card.code;

        if (this.cardNumber && this.cardName && this.cardDate && this.cardCode) {
          this.isSaveCard = true;
        }
      },

      saveStep() {
        this.$store.commit('global/updateOrder', {
            isSaveCard: this.isSaveCard,
        });

        this.$store.commit('global/updateUser', {
          card: {
            number: this.isSaveCard ? this.cardNumber : '',
            name: this.isSaveCard ? this.cardName : '',
            date: this.isSaveCard ? this.cardDate : '',
            code: this.isSaveCard ? this.cardCode : '',
          }
        });

        this.$router.push({ path: '/check-order' });
      },

      PayByCard() {
        this.$v.$touch();
        if (this.$v.$invalid || this.isLoadDisabled) return;

        let data = {
          card: {
            number: this.isSaveCard ? this.cardNumber : '',
            name: this.isSaveCard ? this.cardName : '',
            date: this.isSaveCard ? this.cardDate : '',
            code: this.isSaveCard ? this.cardCode : '',
          },
          payment: {}
        };

        this.isLoadDisabled = true;

        this.axios.post(this.postUrl, data)
          .then(response => {
            this.isLoadDisabled = false;
            this.saveStep();
            this.$router.push({ path: '/order-check' });

          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      }
    },

    mounted() {
      if (!this.cart.length) {
        this.$router.push({ path: '/' });
      }

      this.setTitle(`Оплата картой — ${this.positionsTotalPrice} ₽`);
      this.setBackUrl('/payment');
      this.setWhiteBg();
      this.useCustomFooter();
      this.fillStep();
    },
  }
</script>

<style lang="scss">

</style>


