import Focus from './Focus.js';
import SelectPlaceholder from './SelectPlaceholder.js';
import InfiniteScroll from './InfiniteScroll.js';
import PullToRefresh from './PullToRefresh.js';
import FloatingPlaceholder from './FloatingPlaceholder.js';
import WordEnds from './WordEnds.js';

let directives = {
    'focus': Focus,
    'select-placeholder': SelectPlaceholder,
    'floating-placeholder': FloatingPlaceholder,
    'infinite-scroll': InfiniteScroll,
    'pull-to-refresh': PullToRefresh,
    'word-ends': WordEnds,
};

let VuePlugin = {
    install: function install(Vue) {
        if (Vue._directives_installed) return;

        Vue._directives_installed = true;

        for (let name in directives) {
            Vue.directive(name, directives[name]);
        }
    }
};

export default VuePlugin;


