<template>
    <div class="order-done text-center">
        <figure class="d-inline-block rounded-circle mb-4">
            <img src="../../assets/img/logo.png"
                 srcset="../../assets/img/logo@2x.png 2x"
                 width="85"
                 height="85">
            <div class="rounded-circle bg-success text-white d-inline-block">
                <c-icon name="check"/>
            </div>
        </figure>
        <h1 class="h2 mb-4">Ваш заказ принят!</h1>
        <div class="text-muted pb-6">Оператор свяжется с вами<br>в течение 5 минут</div>
        <router-link to="/" tag="strong" class="text-uppercase text-danger afake">Вернуться к меню</router-link>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'orderDone',
    mixins: [RouteHelper],
    data() {
      return {

      }
    },
    mounted() {
      this.useRawTemplate();
      this.setWhiteBg();
    }
  }
</script>

<style lang="scss">
    @import '../../bootconf.scss';
    @import '~bootstrap/scss/bootstrap';

    .order-done {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-flow: column;
        min-height: 100vh;

        figure {
            position: relative;
            border: 5px solid rgba(0, 0, 0, 0.15);

            img {
                max-width: 100%;
                max-height: 100%;
            }

            .rounded-circle {
                position: absolute;
                top: -4px;
                right: -4px;
                background-color: #21b956 !important;
                border: 2px solid $white;
            }
        }

        .afake {
            position: absolute;
            margin-top: 170px;
        }
    }
</style>


