<template>
    <header class="header" :class="{'bg-white': !transparent, 'inverse-controls': inverseControls}">
        <div class="container">
            <div class="header-nav pt-3 pb-3" :class="{'text-muted': !transparent, 'text-light': transparent}">
                <router-link :to="backUrl || ''"
                             tag="div"
                             class="header-control"
                             :class="{'inverse': inverseControls, 'invisible': !homePage && !backUrl}">
                    <c-icon name="back" v-show="!homePage && !showClose"/>
                    <c-icon name="home" v-show="homePage"/>
                    <c-icon name="close" v-show="showClose"/>
                </router-link>
                <div class="header-title text-center">
                    <h3 class="mb-0 strong">{{ title }}</h3>
                    <h4 class="mt-1 m-0" v-show="subtitle">{{ subtitle }}</h4>
                </div>
                <b-link :href="'tel:' + phone" class="header-control" :class="{'inverse': inverseControls, 'invisible': hidePhone}">
                    <c-icon name="phone"/>
                </b-link>
            </div>
        </div>
    </header>
</template>

<script>
    export default {
        name: 'c-header',
        props: ['title', 'backUrl', 'subtitle', 'homePage', 'phone', 'transparent', 'inverseControls', 'hidePhone', 'showClose'],
        data() {
          return {

          }
        },
    }
</script>

<style lang="scss">
    @import '../../bootconf.scss';

    .header {
        position: relative;
        box-shadow: none;
        z-index: 10;

        &.bg-white {
            box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.12);

            .header-control {
                box-shadow: none;
            }
        }

        &-nav {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        &-control {
            position: relative;
            display: inline-block;
            width: 36px;
            height: 36px;
            border-radius: 50%;
            background: rgba(255, 255, 255, .9);
            box-shadow: 0 2px 9px 0 rgba(0, 0, 0, .2);
            vertical-align: middle;
            cursor: pointer;
            color: $muted !important;

            .icon {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
            }

            &:hover {
                opacity: .9;
                box-shadow: 0 2px 9px 0 rgba(0, 0, 0, .3);
            }

            &:active {
                opacity: .7;
                box-shadow: 0 2px 9px 0 rgba(0, 0, 0, .4);
            }

            &.inverse {
                color: $white !important;
                background-color: transparent;
                box-shadow: none;

                &:hover {
                    box-shadow: none;
                    opacity: .7;
                }
            }
        }
    }
</style>


