export default {
    inserted: (el) => {
        let { placeholder, id } = el;

        if (!placeholder) {
          placeholder = el.getAttribute('placeholder');
        }
        el.insertAdjacentHTML('afterEnd', '<label class="form-control-placeholder" for="' + id + '">' + placeholder + '</label>');
        el.setAttribute('required', true);

        el.placeholder = '';
    }
}


