import Vue from 'vue';
import Router from 'vue-router';

import Demo from '@/components/view/_Demo';
import Index from '@/components/view/Index';
import Category from '@/components/view/Category';
import Product from '@/components/view/Product';
import Cart from '@/components/view/Cart';
import AddAddress from '@/components/view/AddAddress';
import Delivery from '@/components/view/Delivery';
import Payment from '@/components/view/Payment';
import CardPay from '@/components/view/CardPay';
import Orders from '@/components/view/Orders';
import Order from '@/components/view/Order';
import OrderCheck from '@/components/view/OrderCheck';
import OrderDone from '@/components/view/OrderDone';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/demo',
      name: 'Demo',
      component: Demo
    },

    {
      path: '/',
      name: 'Index',
      component: Index,
    },

    {
      path: '/category/:id',
      name: 'Category',
      component: Category,
    },

    {
      path: '/product/:id',
      name: 'Product',
      component: Product,
    },

    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
    },

    {
      path: '/add-address/:id',
      name: 'AddAddress',
      component: AddAddress,
    },

    {
      path: '/delivery',
      name: 'Delivery',
      component: Delivery,
    },

    {
      path: '/payment',
      name: 'Payment',
      component: Payment,
    },

    {
      path: '/card-pay',
      name: 'CardPay',
      component: CardPay,
    },

    {
      path: '/orders',
      name: 'Orders',
      component: Orders,
    },

    {
      path: '/order/:id',
      name: 'Order',
      component: Order,
    },

    {
      path: '/order-check',
      name: 'OrderCheck',
      component: OrderCheck,
    },

    {
      path: '/order-done',
      name: 'OrderDone',
      component: OrderDone,
    },

  ]
});

router.beforeEach((to, from, done) => {
  to.meta.backUrl = from.path;
  done();
});

router.afterEach((to, from) => {
  window.scrollTo(0, 0);
});

export default router;


