<template>
    <div class="pb-6">
        <figure class="bg-white item-figure mb-0">
            <img :src="getImageUrl(pic[0])"
                 :srcset="getImageUrl(pic[0]) + ' 2x'"
                 :alt="name">
        </figure>
        <section class="bg-white pt-3 pb-5 mb-5 border-bottom item-desc">
          <b-container>
              <div class="h4 text-muted mb-1">{{ category.name }}</div>
              <h1 class="strong mb-4">{{ name }}</h1>
              <b-row class="mb-4">
                  <b-col class="col-5 col-sm-auto pr-2">
                      <b-form-group class="mb-0 mt-1"
                                    label-for="totalCount">
                          <b-form-input id="totalCount"
                                        class="mb-2"
                                        type="text"
                                        v-model="totalCount"
                                        readonly>
                          </b-form-input>
                          <div class="h4 mb-0 text-muted text-nowrap">× {{ price }} ₽ за {{ count }} {{ units }}</div>
                      </b-form-group>
                  </b-col>
                  <b-col class="pl-0">
                      <b-button-group>
                          <b-button variant="outline-danger" class="btn-square" @click="decreaseCount">-</b-button>
                          <b-button variant="outline-danger" class="btn-square" @click="increaseCount">+</b-button>
                      </b-button-group>
                  </b-col>
                  <b-col class="text-right mt-2">
                      <div class="strong">{{ totalPrice }} ₽</div>
                  </b-col>
              </b-row>
              <b-button variant="danger" block class="mb-4" @click="addToCart">Положить в корзину</b-button>
              <div class="h4"
                   v-for="(item, index) in desc"
                   :class="{'mb-0': index == (desc.length - 1), 'pt-1': index == 0}">
                 {{ item }}
              </div>
          </b-container>
        </section>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'item',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/product',
        useParamsId: true,

        name: '',
        category: '',
        desc: '',
        price: '',
        count: '',
        units: '',
        pic: [],
        preview: [],

        totalPrice: '',
        totalCount: '',
      }
    },

    methods: {
      prepareData(data) {
        if (!data) return;

        this.id = data.id;
        this.name = data.name;
        this.category = data.category;
        this.desc = data.desc;
        this.price = data.price;
        this.count = data.count;
        this.units = data.units;
        this.pic = data.pic;
        this.preview = data.preview;

        this.totalPrice = this.price;
        this.totalCount = this.count + ' ' + this.units;

        this.setBackUrl(`/category/${this.category.id}`, true);
      },

      increaseCount() {
        this.totalPrice = parseFloat(this.totalPrice) + parseFloat(this.price);
        this.totalCount = parseFloat(this.totalCount) + parseFloat(this.count) + ' ' + this.units;
      },

      decreaseCount() {
        if (this.totalPrice == this.price) return;

        this.totalPrice = parseFloat(this.totalPrice) - parseFloat(this.price);
        this.totalCount = parseFloat(this.totalCount) - parseFloat(this.count) + ' ' + this.units;
      },

      addToCart() {
        this.$store.commit('global/addToCart', {
          id: this.id,
          name: this.name,
          category: this.category,
          price: this.price,
          count: this.count,
          units: this.units,
          pic: this.preview,
          totalPrice: this.totalPrice,
          totalCount: this.totalCount,
        });

        this.totalPrice = this.price;
        this.totalCount = this.count + ' ' + this.units;
      },

      getImageUrl(url) {
        if (!url) return;
        return require(`../../${url}`);
      }
    },

    mounted() {
        this.setTransparentHeader();
    }
  }
</script>

<style lang="scss">
    .item-figure {
        position: relative;
        height: 235px;
        margin-top: -66px;
        overflow: hidden;

        img {
            width: 100%;
            height: auto;
            min-height: 100%;
        }
    }
</style>


