export default function(el, binding) {
    const number = binding.value.toString();
    const words = binding.arg.split('|');
    const groups = ['1', '2, 3, 4', '5, 6, 7, 8, 9, 0'];

    let value = number;

    groups.forEach(function (group, index) {
        if (group.indexOf(number) === -1) return;
        value += ' ' + words[index];
    });

    el.innerHTML = value;
}


