<template>
    <div>
        <b-form>
            <b-tabs nav-class="h4 bg-light" v-on:input="changeTabIndex" :value="deliveryType">
                <b-tab title="Доставка">
                    <section class="pt-4 address-list border-bottom">
                        <b-container class="pr-0">
                            <div class="text-muted mb-1">Адрес доставки</div>
                            <ul class="list-unstyled">
                                <li v-for="item in delivery">
                                    <b-form-group class="mb-0" :state="deliveryType == 0 && !$v.deliveryAddress.$error">
                                        <b-form-radio :value="item.id"
                                                      v-model="deliveryAddress"
                                                      class="align-middle"
                                                      @change="selectAddress(item.id)">
                                            <div class="pb-4 pt-4 border-bottom pr-6">{{ item | FormatAddress }}</div>
                                        </b-form-radio>
                                    </b-form-group>
                                    <router-link :to="{ name: 'AddAddress', params: { id: item.id }}">
                                        <c-icon name="edit" class="text-muted"/>
                                    </router-link>
                                </li>
                            </ul>
                            <div class="text-center p-4">
                                <router-link to="/add-address/new" tag="strong" class="text-uppercase text-danger afake">добавить адрес</router-link>
                            </div>
                        </b-container>
                    </section>
                </b-tab>
                <b-tab title="Самовывоз">
                    <section class="bg-white pt-5 pb-3">
                        <b-container>
                            <b-form-group id="exampleInputGroup1"
                                          class="mb-0"
                                          :state="deliveryType == 1 && !$v.deliveryPoint.$error"
                                          label-for="exampleInput1">
                                <b-form-select :options="deliveryPoints"
                                               v-model="deliveryPoint"
                                               id="select1"
                                               class="mb-3"
                                               placeholder="Пункт выдачи заказа"
                                               v-floating-placeholder/>
                            </b-form-group>
                        </b-container>
                    </section>
                </b-tab>
            </b-tabs>
            <div class="p-1 bg-light"></div>
            <section class="pt-5 pb-6">
                <b-container>
                    <b-form-group label-for="tel" :state="!$v.tel.$error">
                        <b-form-input id="tel"
                                      type="text"
                                      required
                                      v-model="tel"
                                      v-floating-placeholder
                                      placeholder="Ваш телефон">
                        </b-form-input>
                    </b-form-group>
                    <b-form-group label-for="email" :state="!$v.email.$error">
                        <b-form-input id="email"
                                      type="text"
                                      v-model="email"
                                      required
                                      v-floating-placeholder
                                      placeholder="Ваша электронная почта">
                        </b-form-input>
                    </b-form-group>
                    <b-form-group label-for="comment">
                        <b-form-input id="comment"
                                      type="text"
                                      v-model="comment"
                                      required
                                      v-floating-placeholder
                                      placeholder="Комментарий к заказу">
                        </b-form-input>
                    </b-form-group>
                    <div class="text-center text-muted small pt-2 mb-5" v-if="isDeliveryConditionsVisible">
                        Условия доставки. Натуралистическая парадигма, с другой стороны, отражает тоталитарный тип культуры
                    </div>
                </b-container>
            </section>
        </b-form>
        <c-footer>
            <b-button variant="danger" size="lg" block class="rounded-0" @click="saveStep">Оплатить {{ positionsTotalPrice }} ₽</b-button>
        </c-footer>
    </div>
</template>

<script>
  import { required, minLength } from 'vuelidate/lib/validators';
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'delivery',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        email: '',
        tel: '',
        comment: '',
        deliveryType: 0,
        deliveryAddress: null,
        isDeliveryConditionsVisible: true,
        deliveryPoint: null,
      }
    },
    computed: {
      cart() {
        return this.$store.getters['global/user'].cart || [];
      },
      positionsTotalPrice() {
        return this.cart.reduce((total, current) => total + parseFloat(current.totalPrice), 0);
      },
      delivery() {
        return this.$store.getters['global/user'].delivery || [];
      },
      order() {
        return this.$store.getters['global/user'].order;
      },
      deliveryPoints() {
        return this.$store.getters['global/shop'].deliveryPoints;
      }
    },
    validations() {
      let rules = {
        tel: {
          required,
          minLength: minLength(4)
        },
        email: {
          required,
          minLength: minLength(4)
        }
      };

      if (this.deliveryType == 0) {
        rules.deliveryAddress = {
          required,
        };
      } else {
        rules.deliveryPoint = {
          required,
        };
      }

      return rules;
    },
    mounted() {
      if (!this.cart.length) {
        this.$router.push({ path: '/' });
      }

      this.setTitle('Способ получения');
      this.setBackUrl('/cart');
      this.setWhiteBg();
      this.useCustomFooter();
      this.fillStep();
    },
    methods: {
        changeTabIndex(tab_index) {
          this.deliveryType = tab_index;
          this.isDeliveryConditionsVisible = tab_index === 0;
        },

        selectAddress(id) {
            this.deliveryAddress = id;
        },

        fillStep() {
            this.tel = this.order.tel;
            this.email = this.order.email;
            this.comment = this.order.comment;
            this.deliveryType = this.order.deliveryType;
            this.deliveryPoint = this.order.deliveryPoint;
            this.deliveryAddress = this.order.deliveryAddress;
        },

        saveStep() {
          this.$v.$touch();
          if (this.$v.$invalid) return;

          this.$store.commit('global/updateOrder', {
            tel: this.tel,
            email: this.email,
            comment: this.comment,
            deliveryType: this.deliveryType,
            deliveryPoint: this.deliveryPoint,
            deliveryAddress: this.deliveryAddress,
          });

          this.$router.push({ path: '/payment' });
        },
    }
  }
</script>

<style lang="scss">
    .address-list {
        li {
            position: relative;

            .custom-radio,
            .custom-control-label {
                display: block;
            }

            .icon {
                position: absolute;
                right: 15px;
                top: 0;
                bottom: 0;
                margin: auto;
            }
        }
    }
</style>


