<template>
    <div class="user-panel text-muted text-center">
        <router-link :to="item.route" tag="div" class="user-panel-item" v-for="(item, index) in menu" :key="`menuNav_${index}`">
            <figure class="m-0">
                <c-icon :name="item.icon"/>
                <b-badge pill variant="danger" class="badge-s" v-if="item.count">{{ item.count }}</b-badge>
            </figure>
            <small>{{ item.name }}</small>
        </router-link>
    </div>
</template>

<script>
    export default {
        name: 'c-user-panel',
        props: ['cart', 'orders'],
        data() {
          return {}
        },
        computed: {
            menu() {
                const self = this;

                return [
                    {
                        name: 'Меню',
                        route: '/',
                        icon: 'menu',
                        count: null,
                    },
                    {
                        name: 'Корзина',
                        route: '/cart',
                        icon: 'cart',
                        count: self.cart,
                    },
                    {
                        name: 'Заказы',
                        route: '/orders',
                        icon: 'orders',
                        count: self.orders,
                    }
                ]
            }
        }
    }
</script>

<style lang="scss">
    @import '../../bootconf.scss';

    .user-panel {
        display: flex;
        box-shadow: 0 -1px 8px 0 rgba(0, 0, 0, 0.08);

        &-item {
            position: relative;
            flex-grow: 1;
            flex-basis: 0;
            padding: 6px 0;
            min-height: 56px;
            cursor: pointer;
            transition: .15s color ease-in-out;

            &.router-link-exact-active,
            &:hover {
                color: $body-color;
            }
        }

        .badge {
            position: absolute;
            margin-left: -5px;
        }
    }
</style>


