<template>
    <div class="pb-6" v-pull-to-refresh>
        <section class="mb-5">
           <b-container class="pr-0">
               <ul class="list-unstyled">
                   <li class="pb-4" v-for="(item, index) in items" :class="{'pt-4': index == 0}">
                       <b-row class="mr-0">
                           <b-col class="col-auto pr-0">
                              <router-link :to="{ name: 'Product', params: { id: item.id }}" class="afake">
                                  <img :src="getImageUrl(item.pic[0])"
                                       :srcset="getImageUrl(item.pic[1]) + ' 2x'"
                                       :alt="item.name"
                                       width="128"
                                       height="128"
                                       class="rounded mr-3 mb-4">
                              </router-link>
                           </b-col>
                           <b-col class="pl-0" :class="{'border-bottom': items.length != ++index}">
                               <div class="pb-4 pl-0">
                                   <router-link :to="{ name: 'Product', params: { id: item.id }}" tag="div" class="afake text-dark strong mb-3">{{ item.name }}</router-link>
                                   <c-label variant="danger" class="mr-2">{{ item.price }} P</c-label>
                                   <span class="h4 text-muted">за {{ item.count }} {{ item.units }}</span>
                               </div>
                           </b-col>
                       </b-row>
                   </li>
               </ul>
           </b-container>
        </section>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';
  import FetchHelper from '../../mixins/FetchHelper.js';

  export default {
    name: 'list',
    mixins: [FetchHelper, RouteHelper],
    data() {
      return {
        fetchUrl: '/api/category',
        useParamsId: true,
        items: []
      }
    },

    methods: {
      prepareData(data) {
        if (!data) return;

        this.items = data;
      },

      getImageUrl(url) {
        if (!url) return;
        return require(`../../${url}`);
      },

      setCustomTitle() {
        const categories = this.$store.getters['global/shop'].categories;
        const categoryId = this.$route.params.id;

        if (!categories || !categoryId) return;

        const currentCatetory = categories.filter((item) => item.id == categoryId);
        this.setTitle(currentCatetory[0].name);
      }
    },

    mounted() {
        this.setBackUrl('/');
        this.setWhiteBg();
        this.setCustomTitle();
    },

    computed: {
      categories() {
        return this.$store.getters['global/shop'].categories;
      },
    },

    watch: {
        categories() {
          this.setCustomTitle();
        }
    }
  }
</script>

<style lang="scss">

</style>


