<template>
    <div>
        <b-form>
            <section class="border-bottom pt-4">
                <b-container>
                    <b-row>
                        <b-col>
                            <b-form-group class="mb-1">
                                <b-form-radio name="isCardPay" v-model="isCardPay" v-on:change="setIsCardPay" :value="false">
                                    При получении
                                    <div class="h4 text-muted mt-1">курьеру — наличными или картой</div>
                                </b-form-radio>
                            </b-form-group>
                        </b-col>
                        <b-col class="text-right" v-show="!isCardPay && !isNeedSurrender">
                            <span class="d-inline-block text-info afake mr-3" @click="setIsNeedSurrender(true)">Сдача нужна</span>
                        </b-col>
                    </b-row>
                    <b-row class="mt-3" v-show="!isCardPay && isNeedSurrender">
                        <b-col class="col-6 col-sm-4 col-md-4 col-lg-3 ml-5 pr-0">
                            <b-form-group id="exampleInputGroup1"
                                          class="ml-1 mb-0"
                                          :state="!isCardPay && isNeedSurrender && !$v.surrender.$error"
                                          label-for="exampleInput1">
                                <b-form-select :options="surrenderValues"
                                               v-model="surrender"
                                               id="select1"
                                               class="mb-3"
                                               placeholder="Нужна сдача с..."
                                               v-floating-placeholder/>
                            </b-form-group>
                        </b-col>
                        <b-col class="text-right" v-if="!isCardPay && isNeedSurrender">
                            <span class="d-inline-block text-info mt-2 mr-3 afake" @click="setIsNeedSurrender(false)">Сдача не&nbsp;нужна</span>
                        </b-col>
                    </b-row>
                    <div class="ml-5 pl-1 mt-2 mb-4">
                        <div class="border-bottom"></div>
                    </div>
                    <b-form-group class="mb-0">
                        <b-form-radio name="isCardPay" v-model="isCardPay" v-on:change="setIsCardPay" :value="true">
                            Сейчас
                            <div class="h4 text-muted mt-1">в приложении — картой</div>
                        </b-form-radio>
                    </b-form-group>
                </b-container>
            </section>
            <div class="p-1 bg-light"></div>
            <section class="pt-3 pb-4">
                <b-container>
                    <b-form-group>
                        <label for="isSaveCheck" class="pl-0 col col-auto col-form-label">Получить электронный чек</label>
                        <b-form-checkbox class="float-right" id="isSaveCheck" v-model="isSaveCheck"></b-form-checkbox>
                    </b-form-group>
                    <b-form-group v-show="isSaveCheck"
                                  :state="isSaveCheck && !$v.emailForCheck.$error"
                                  label-for="emailForCheck">
                        <b-form-input id="emailForCheck"
                                      type="text"
                                      required
                                      v-model="emailForCheck"
                                      v-floating-placeholder
                                      placeholder="Email для чека">
                        </b-form-input>
                    </b-form-group>
                </b-container>
            </section>
            <c-footer>
                <b-button variant="danger" size="lg" block class="rounded-0" @click="saveStep">Оплатить {{ positionsTotalPrice }} ₽</b-button>
            </c-footer>
        </b-form>
    </div>
</template>

<script>
  import { required, minLength } from 'vuelidate/lib/validators';
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'delivery',
    mixins: [RouteHelper],
    data() {
      return {
        isCardPay: false,
        isNeedSurrender: false,
        isSaveCheck: false,
        surrender: null,
        surrenderValues: [],
        emailForCheck: '',
      }
    },
    computed: {
      cart() {
        return this.$store.getters['global/user'].cart || [];
      },
      shop() {
        return this.$store.getters['global/shop'];
      },
      order() {
        return this.$store.getters['global/user'].order || [];
      },
      positionsTotalPrice() {
        return this.cart.reduce((total, current) => total + parseFloat(current.totalPrice), 0);
      }
    },
    watch: {
      isSaveCheck() {
        if (this.isSaveCheck) return;
        this.emailForCheck = '';
      }
    },
    validations() {
      let rules = {};

      if (this.isSaveCheck) {
        rules.emailForCheck = {
          required,
          minLength: minLength(4)
        };
      }

      if (this.isNeedSurrender && !this.isCardPay) {
        rules.surrender = {
          required,
        };
      }

      return rules;
    },
    mounted() {
        if (!this.cart.length) {
          this.$router.push({ path: '/' });
        }

        this.setTitle('Способ оплаты');
        this.setBackUrl('/delivery');
        this.setWhiteBg();
        this.useCustomFooter();
        this.fillStep();
    },
    methods: {
      setIsCardPay(isEnable) {
        this.isCardPay = isEnable;
      },

      setIsNeedSurrender(isEnable) {
        this.isNeedSurrender = isEnable;
        if (!isEnable) {

          this.surrender = null;
        }
      },

      fillStep() {
        this.isCardPay = this.order.isCardPay;
        this.surrender = this.order.surrender;
        this.emailForCheck = this.order.emailForCheck;
        this.surrenderValues = this.shop.surrender.map(function(variant) {
          return {
            value: variant,
            text: `${variant} ₽`
          }
        });
        if (this.surrender) {
          this.isNeedSurrender = true;
        }

        if (this.emailForCheck) {
          this.isSaveCheck = true;
        }
      },

      saveStep() {
        this.$v.$touch();
        if (this.$v.$invalid) return;

        this.$store.commit('global/updateOrder', {
          isCardPay: this.isCardPay,
          surrender: this.surrender,
          emailForCheck: this.emailForCheck,
        });

        if (this.isCardPay) {
          this.$router.push({ path: '/card-pay' });
        } else {
          this.$router.push({ path: '/order-check' });
        }
      },
    }
  }
</script>

<style lang="scss">

</style>


