<template>
    <b-container class="pt-6 mt6 pb-6">
        <h1 class="h2 mt-2 mb-6 text-center">Пожалуйста,<br>проверьте детали:</h1>
        <ul class="list-unstyled mb-3">
            <li class="pt-3 pb-3">
                <div class="text-muted h4 mb-1">Заказ</div>
                <div><span v-word-ends:позиция|позиции|позиций="cart.length"></span> на сумму {{ positionsTotalPrice }} ₽</div>
            </li>
            <li class="pt-3 pb-3">
                <div class="text-muted h4 mb-1">Оплата</div>
                <div class="d-flex justify-content-between">
                    <div>
                        <span v-if="order.isCardPay">Картой в приложении</span>
                        <span v-if="!order.isCardPay">Наличными курьеру</span>
                    </div>
                    <div class="h4 mb-0 text-success text-right" v-if="order.isCardPay">Оплачено</div>
                </div>
            </li>
            <li class="pt-3 pb-3" v-if="order.surrender">
                <div class="text-muted h4 mb-1">Нужна сдача с</div>
                <div class="d-flex justify-content-between">
                    <div>
                        {{ order.surrender }} ₽
                    </div>
                </div>
            </li>
            <li class="pt-3 pb-3">
                <div class="text-muted h4 mb-1">Способ получения</div>
                <div>
                    <span v-if="order.deliveryType == 0">Доставка</span>
                    <span v-if="order.deliveryType == 1">Самовывоз</span>
                </div>
            </li>
            <li class="pt-3 pb-3">
                <div class="text-muted h4 mb-1">
                    <span v-if="order.deliveryType == 0">Адрес доставки</span>
                    <span v-if="order.deliveryType == 1">Пункт самовывоза</span>
                </div>
                <div>
                    <span v-if="order.deliveryType == 0">{{ deliveryAddress | FormatAddress }}</span>
                    <span v-if="order.deliveryType == 1">{{ deliveryPointTitle }}</span>
                </div>
            </li>
        </ul>
        <b-button variant="danger" size="lg" block class="mb-4" @click="sendOrder">Все верно! Заказать</b-button>
        <div class="text-center">
            <router-link to="/cart" tag="strong" class="afake text-uppercase text-danger">Кое-что изменить</router-link>
        </div>
    </b-container>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'orderCheck',
    mixins: [RouteHelper],
    data() {
      return {
        postUrl: '/api/send_order',
        isLoadDisabled: false,
      }
    },
    computed: {
      cart() {
        return this.$store.getters['global/user'].cart || [];
      },

      delivery() {
        return this.$store.getters['global/user'].delivery || [];
      },

      order() {
        return this.$store.getters['global/user'].order || {};
      },

      shop() {
        return this.$store.getters['global/shop'] || {};
      },

      positionsTotalPrice() {
        return this.cart.reduce((total, current) => total + parseFloat(current.totalPrice), 0);
      },

      deliveryPointTitle() {
        if (!this.order || typeof this.order.deliveryPoint === 'undefined') return '';
        return this.shop.deliveryPoints.sort((item) => item.id == this.order.deliveryPoint)[0].text;
      },

      deliveryAddress() {
        if (typeof this.delivery === 'undefined' || typeof this.order.deliveryAddress === 'undefined') return '';
        return this.delivery.sort((item) => item.id == this.order.deliveryAddress)[0];
      }
    },
    methods: {
      sendOrder() {
        if (this.isLoadDisabled) return;

        let order = {
          email: this.order.email,
          tel: this.order.tel,
          comment: this.order.comment,
          items: this.cart,
          deliveryType: this.order.deliveryType,
          deliveryPoint: this.order.deliveryPoint,
          deliveryAddress: this.order.deliveryAddress,
          surrender: this.order.surrender,
        };

        this.isLoadDisabled = true;

        this.axios.post(this.postUrl, order)
          .then(response => {
            this.isLoadDisabled = false;
            this.$store.commit('global/clearCart');
            this.$store.commit('global/dropOrder');
            this.$store.commit('global/incrementOrders');
            this.$router.push({ path: '/order-done' });

          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      }
    },
    mounted() {
      if (!this.cart.length) {
        this.$router.push({ path: '/' });
      }

      this.useRawTemplate();
      this.setWhiteBg();
    }
  }
</script>

<style lang="scss">

</style>


