<template>
    <div :class="className">
        <slot/>
    </div>
</template>

<script>
    export default {
        name: 'c-label',
        props: ['variant'],
        computed: {
            className() {
                let className = 'label';

                if (this.variant) {
                    className += ' label-' + this.variant;
                }

                return className;
            }
        }
    }
</script>

<style lang="scss">
    @import '../../bootconf.scss';

    .label {
        display: inline-block;
        padding: $btn-padding-y $btn-padding-x;
        border: 1px solid;
        border-radius: $border-radius;
        line-height: $btn-line-height;
        vertical-align: middle;

        &-danger {
            border-color: $danger;
            color: $danger;
        }

        &-success {
            border-color: $success;
            color: $success;
        }

        &-muted {
            border-color: $muted;
            color: $muted;
        }
    }
</style>


