<template>
    <svg class="icon" :data-title="name">
        <use :xlink:href="nameHash"></use>
    </svg>
</template>

<script>
    export default {
        name: 'c-icon',
        props: ['name'],
        computed: {
            nameHash() {
                return '#' + this.name;
            }
        }
    }
</script>

<style lang="scss">
    .icon {
        display: inline-block;
        width: 24px;
        height: 24px;
        vertical-align: top;
        fill: currentColor;
    }
</style>


