<template>
    <div class="pb-6">
        <figure class="shop-figure mb-0">
            <img :src="banner ? getImageUrl(banner[0]) : null"
                 :srcset="banner ? (getImageUrl(banner[1]) + ' 2x') : null"
                 :alt="name">
            <b-container class="pr-0">
                <figcaption class="text-white pb-3">
                    <h1 class="strong mb-1">{{ name }}</h1>
                    <h4 class="m-0">{{ type }}</h4>
                </figcaption>
                <aside class="bg-success p-3 text-white">
                    <div class="h1 mb-0">{{ cashback }}%</div>
                    <div class="small">кэшбэк</div>
                </aside>
            </b-container>
        </figure>
        <section class="order-box bg-white pt-3 pb-3 mb-2 border-bottom">
            <b-container class="d-flex align-items-center">
                <figure class="mb-0 p-3 mr-3 bg-danger rounded-circle">
                    <img src="../../assets/img/order-box.png"
                         srcset="../../assets/img/order-box@2x.png 2x"
                         alt="Заказы"
                         width="36"
                         height="36">
                </figure>
                <div>
                    <div class="h2 mb-1">У вас <span v-word-ends:заказ|заказа|заказов="orders ? orders.confirmed : 0"></span></div>
                    <div class="h4 text-muted mb-0">
                        +<span v-word-ends:заказ|заказа|заказов="orders ? orders.unconfirmed : 0"></span>, еще не&nbsp;подтвержденный оператором заведения
                    </div>
                </div>
            </b-container>
        </section>
        <section class="shop-menu bg-white mb-5 border-bottom">
           <b-container class="pr-0">
               <ul class="list-unstyled">
                   <router-link v-for="(category, index) in categories"
                                :to="{ name: 'Category', params: { id: category.id }}"
                                :class="{'border-bottom': ++index != categories.length}"
                                tag="li"
                                class="pt-3 pb-3 d-flex align-content-center justify-content-between text-dark afake">
                       <div class="m-0">{{ category.name }}</div>
                       <c-icon name="goto" class="mr-3 text-muted"/>
                   </router-link>
               </ul>
           </b-container>
        </section>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'index',
    mixins: [RouteHelper],
    data() {
      return {

      }
    },

    methods: {
        getImageUrl(url) {
            if (!url) return;
            return require(`../../${url}`);
        }
    },

    computed: {
        name() {
            return this.$store.getters['global/shop'].name;
        },
        banner() {
            return this.$store.getters['global/shop'].banner;
        },
        type() {
            return this.$store.getters['global/shop'].type;
        },
        categories() {
            return this.$store.getters['global/shop'].categories;
        },
        cashback() {
            return this.$store.getters['global/user'].cashback;
        },
        orders() {
            return this.$store.getters['global/user'].orders;
        }
    },

    mounted() {
        this.setTitle('Меню заведения');
        this.setTransparentHeader();
        this.inverseHeaderControls();
    }
  }
</script>

<style lang="scss">
    .shop-figure {
        position: relative;
        height: 235px;
        margin-top: -66px;
        overflow: hidden;

        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, .5);
        }

        .container {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            display: flex;
            justify-content: space-between;
        }

        img {
            width: 100%;
            height: auto;
            min-height: 100%;
        }

        aside {
            border-top-left-radius: 15px;
        }
    }

    .shop-menu {
        li {
            line-height: 1.5;
        }

        .text-muted {
            opacity: .4;
        }
    }
</style>


