<template>
    <div class="pb-5">
        <div class="p-3 text-center">
            <span class="text-muted h4" v-if="cart.length"><span v-word-ends:позиция|позиции|позиций="cart.length"></span> на сумму {{ positionsTotalPrice }} ₽</span>
            <span class="text-muted h4" v-if="!cart.length">Корзина пуста</span>
        </div>
        <section class="bg-white mb-6" v-if="cart.length">
            <b-container class="pr-0">
                <ul class="list-unstyled">
                    <li class="pt-4 pb-4" v-for="(item, index) in cart">
                        <b-row class="mr-0">
                            <b-col class="col-auto pr-0">
                               <router-link :to="{ name: 'Product', params: { id: item.id }}" class="afake">
                                   <img :src="getImageUrl(item.pic[0])"
                                        :srcset="getImageUrl(item.pic[0]) + ' 2x'"
                                        :alt="item.name"
                                        width="128"
                                        height="128"
                                        class="rounded mr-3 mb-4">
                               </router-link>
                            </b-col>
                            <b-col class="pl-0" :class="{'border-bottom': (index != cart.length -1)}">
                                <div class="pb-4 pl-0">
                                    <router-link :to="{ name: 'Product', params: { id: item.id }}" tag="div" class="afake text-dark strong mb-3">{{ item.category.name }} {{ item.name }}</router-link>
                                    <b-row class="mb-4 mr-0">
                                        <b-col class="col-6 col-sm-auto pr-2">
                                            <b-form-group id="exampleInputGroup1"
                                                          class="mb-0 mt-1"
                                                          label-for="exampleInput1">
                                                <b-form-input id="exampleInput1"
                                                              class="mb-2"
                                                              type="text"
                                                              v-model="item.totalCount"
                                                              readonly>
                                                </b-form-input>
                                                <div class="h4 mb-0 text-muted text-nowrap">× {{ item.price }} ₽ за {{ item.count }} {{ item.units }}</div>
                                            </b-form-group>
                                        </b-col>
                                        <b-col class="pl-0 pr-0 pr-xs-3 text-right">
                                            <b-button-group>
                                                <b-button variant="outline-danger" class="btn-square" @click="decreaseCount(item, index)">-</b-button>
                                                <b-button variant="outline-danger" class="btn-square" @click="increaseCount(item)">+</b-button>
                                            </b-button-group>
                                        </b-col>
                                    </b-row>
                                    <div class="strong">
                                        {{ item.totalPrice }} ₽
                                    </div>
                                </div>
                            </b-col>
                        </b-row>
                    </li>
                </ul>
            </b-container>
            <b-container class="pb-5">
                <router-link to="/delivery">
                    <b-button variant="danger" block>Сделать заказ</b-button>
                </router-link>
            </b-container>
        </section>
    </div>
</template>

<script>
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'cart',
    mixins: [RouteHelper],
    data() {
      return {

      }
    },
    computed: {
      cart() {
        return this.$store.getters['global/user'].cart || [];
      },
      positionsTotalPrice() {
        return this.cart.reduce((total, current) => total + parseFloat(current.totalPrice), 0);
      }
    },
    methods: {
      increaseCount(item) {
        item.totalPrice = parseFloat(item.totalPrice) + parseFloat(item.price);
        item.totalCount = parseFloat(item.totalCount) + parseFloat(item.count) + ' ' + item.units;
      },

      decreaseCount(item, index) {
        item.totalPrice = parseFloat(item.totalPrice) - parseFloat(item.price);
        item.totalCount = parseFloat(item.totalCount) - parseFloat(item.count) + ' ' + item.units;

        if (item.totalPrice > 0) return;
        this.$store.commit('global/removeFromCart', index);
      },

      getImageUrl(url) {
        if (!url) return;
        return require(`../../${url}`);
      }
    },
    mounted() {
        this.setTitle('Корзина');
        this.setBackUrl('/');
    }
  }
</script>

<style lang="scss">

</style>


