<template>
    <div class="pt-5 pb-6">
        <b-container>
            <b-form class="mb-5">
                <b-form-group label-for="city"
                              :state="!$v.city.$error">
                    <b-form-input id="city"
                                  type="text"
                                  required
                                  v-model="city"
                                  v-floating-placeholder
                                  placeholder="Город">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="street"
                              :state="!$v.street.$error">
                    <b-form-input id="street"
                                  type="text"
                                  required
                                  v-model="street"
                                  v-floating-placeholder
                                  placeholder="Улица">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="house"
                              :state="!$v.house.$error">
                    <b-form-input id="house"
                                  type="text"
                                  required
                                  v-model="house"
                                  v-floating-placeholder
                                  placeholder="Дом (корпус, строение)">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="flat">
                    <b-form-input id="flat"
                                  type="text"
                                  required
                                  v-model="flat"
                                  v-floating-placeholder
                                  placeholder="Квартира или офис">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="entrance">
                    <b-form-input id="entrance"
                                  type="text"
                                  required
                                  v-model="entrance"
                                  v-floating-placeholder
                                  placeholder="Подъезд">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="floor">
                    <b-form-input id="floor"
                                  type="text"
                                  required
                                  v-model="floor"
                                  v-floating-placeholder
                                  placeholder="Этаж">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="code">
                    <b-form-input id="code"
                                  type="text"
                                  required
                                  v-model="code"
                                  v-floating-placeholder
                                  placeholder="Код на двери">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="title">
                    <b-form-input id="title"
                                  type="text"
                                  required
                                  v-model="name"
                                  v-floating-placeholder
                                  placeholder="Название адреса">
                    </b-form-input>
                </b-form-group>
                <b-form-group label-for="comment">
                    <b-form-input id="comment"
                                  type="text"
                                  required
                                  v-model="comment"
                                  v-floating-placeholder
                                  placeholder="Комментарий">
                    </b-form-input>
                </b-form-group>
            </b-form>
        </b-container>
        <c-footer>
            <b-button variant="danger" size="lg" block class="rounded-0" @click="sendRequest">Сохранить адрес</b-button>
        </c-footer>
    </div>
</template>

<script>
  import { required, minLength } from 'vuelidate/lib/validators';
  import RouteHelper from '../../mixins/RouteHelper.js';

  export default {
    name: 'addAddress',
    mixins: [RouteHelper],
    data() {
      return {
        postUrl: '/api/add_address',
        isLoadDisabled: false,

        id: '',
        city: '',
        street: '',
        house: '',
        flat: '',
        entrance: '',
        floor: '',
        code: '',
        name: '',
        comment: '',
      }
    },

    computed: {
      delivery() {
        if (this.isNewAddress) return {};
        return this.$store.getters['global/user'].delivery || {};
      },

      isNewAddress() {
        return this.$route.params.id === 'new';
      }
    },
    watch: {
      delivery() {
        if (this.isNewAddress) return {};
        this.fillModel();
      }
    },

    validations() {
      return {
        city: {
          required
        },
        street: {
          required
        },
        house: {
          required
        }
      };
    },

    methods: {
      fillModel() {
        if (!this.delivery.length) return;

        let currentDelivery = this.delivery.filter((item) => item.id == this.$route.params.id)[0];

        this.id = currentDelivery.id;
        this.city = currentDelivery.city;
        this.street = currentDelivery.street;
        this.house = currentDelivery.house;
        this.flat = currentDelivery.flat;
        this.entrance = currentDelivery.entrance;
        this.floor = currentDelivery.floor;
        this.code = currentDelivery.code;
        this.name = currentDelivery.name;
        this.comment = currentDelivery.comment;
      },

      sendRequest() {
        this.$v.$touch();
        if (this.$v.$invalid || this.isLoadDisabled) return;

        let deliveryAddress = {
          id: this.id,
          city: this.city,
          street: this.street,
          house: this.house,
          flat: this.flat,
          entrance: this.entrance,
          floor: this.floor,
          code: this.code,
          name: this.name,
          comment: this.comment
        };

        this.isLoadDisabled = true;

        this.axios.post(this.postUrl, deliveryAddress)
          .then(response => {
            this.isLoadDisabled = false;
            if (this.isNewAddress) {
              let deliveryItems = this.$store.getters['global/user'].delivery;
              let newAddressId = deliveryItems[deliveryItems.length - 1].id + 1;

              deliveryAddress.id = response.id || newAddressId;

              this.$store.commit('global/addDeliveryAddress', deliveryAddress);
            } else {
              this.$store.commit('global/updateDeliveryAddress', deliveryAddress);
            }
            this.$router.push({path: '/delivery'});
          })
          .catch(e => {
            this.errors.push(e);
            this.isLoadDisabled = false;
          });
      }
    },

    mounted() {
      if (this.isNewAddress) {
        this.setTitle('Новый адрес');
      } else {
        this.setTitle('Редактировать адрес');
        this.fillModel();
      }

      this.setBackUrl('/delivery');
      this.setWhiteBg();
      this.useCustomFooter();
      this.hideHeaderPhone();
      this.showHeaderClose();
    }
  }
</script>

<style lang="scss">

</style>


